.myinfo {
  width: 100%;
}

.myinfo .headtitle {
  width: 100%;
  height: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 0.026667rem solid #666;
}

.myinfo .headtitle i {
  font-size: .5rem;
}

.myinfo .headtitle span {
  font-size: .5rem;
}

.myinfo .itembox {
  width: 100%;
  height: 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 0.026667rem solid #c5c2c2;
}

.myinfo .itembox span {
  color: #666;
  font-size: .25rem;
}

.myinfo .itembox img {
  width: 1.5rem;
}